.trainer-list{max-width:1172px;margin:0 auto;display:flex;flex-wrap:wrap}.trainer-item{display:inline-block;cursor:pointer;width:293px;height:240px;background-color:#FCFBFA;transition:background-color .3s;position:relative}.trainer-item__name{font-size:18px;color:#333;position:absolute;top:108px;left:40px}.trainer-item__position{font-size:14px;color:#807970;position:absolute;top:149px;left:40px}.trainer-item__position>div+div{margin-top:8px}.trainer-item__photo{width:120px;height:120px;background-color:#eee;position:absolute;top:0;right:0}.trainer-item__dot{width:24px;height:24px;background-color:#F47E14;position:absolute;top:108px;right:108px;transition:all .3s}.trainer-item:hover{background-color:#F2F3F5}.trainer-item:hover .trainer-item__dot{top:96px;right:96px}
