.contact-tips{font-size:18px;line-height:1.5;color:#807970;text-align:center}.contact-way-item{display:inline-block;vertical-align:top;width:33.33%;text-align:center;padding:40px 0 36px;border:1px solid #ccc;margin-right:40px;width:calc((100% - 40px * 2) / 3)}.contact-way-item:last-child{margin-right:0}.contact-way-item:hover .way__icon{background-color:#F47E14;color:#fff}@media (max-width: 768px){.contact-way-item{margin-right:0;width:33.33%;border-right-width:0}.contact-way:last-child{border-right-width:1px}}.way__icon{display:inline-block;box-sizing:border-box;width:48px;height:48px;border-radius:24px;border:1px solid #F47E14;color:#F47E14}.way__name{font-size:18px;color:#333;font-weight:500;margin:24px 0 13px}.way__content{font-size:14px;color:#333}
